import React from 'react'

import { AppLink } from './AppLink';
import styled from 'styled-components';


const AppPhoneContainerStyles = styled.div`
  color: red;
  ${ props => (
  props.withIcon && `
  width: 100%;
    &::before: {
      content: "";
      background: url("/svg/phone.svg") no-repeat;
    }
  `) }
`

export const AppPhone = ({ phone }) => (
  <AppPhoneContainerStyles withIcon={ true }>
    <AppLink href={ `tel:${ phone }` }>
      { phone }
    </AppLink>
  </AppPhoneContainerStyles>
)
